let address = "0xD3d20D0Bd31751f084Cb4E4fF98E568c8C2132Fb";
let contractInstance = new web3.eth.Contract(ABI, address);

function getWalletBalance(walletAddress){
    return contractInstance.methods.balanceOf(walletAddress).call()
}

function getOwnerToken(walletAddress, index){
    return contractInstance.methods.ownerToIds(walletAddress, index).call()
}

function getcAttrs(tokenid){
    return contractInstance.methods.cAttributes(tokenid).call()
}

export{
    getWalletBalance,
    getOwnerToken,
    getcAttrs
}