import httpRequest from '@/request/index'

// 获取用户信息
export function apiGetTokenInfo(tokenId) {
    return httpRequest({
		url: '/queryToken/' + tokenId,
		method: 'get',
	})
}

export function apiGetAlbumInfo(albumId) {
    return httpRequest({
		url: '/queryAlbum/' + albumId,
		method: 'get',
	})
}

export function apiGetWallet(key, walletAddress) {
    return httpRequest({
		url: '/queryKey/' + key,
		method: 'get',
        params: {wallet_address: walletAddress}
	})
}

export function apiSetFlag(key) {
    return httpRequest({
		url: '/setFlag/' + key,
		method: 'get'
	})
}