<template>
  <div class="login">

    <el-dialog v-model="dialogVisible" title="公告栏" :lock-scroll="true" width="90%">
      <!-- <el-input v-model="queryKey" placeholder="输入查询码" clearable /> -->
      <el-row>
        <el-col :span="12">
          <img src="@/assets/ding.jpg" alt="" class="image" style="margin: 0 auto; max-height: 360px; max-width: 320px;">
          <div style="margin-top: 20px; text-align: center;">
            IBOX HOTDOG公告监控极速通道，保存二维码，扫码免费进入钉钉群
          </div>
        </el-col>
        <el-col :span="12">
          <img src="@/assets/wx.jpg" alt="" class="image" style="margin: 0 auto; max-height: 360px; max-width: 320px;">
          <div style="margin-top: 20px; text-align: center;">
            加好友,进微信群反馈问题
          </div>
        </el-col>

      </el-row>

      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="setQueryKey">确定</el-button>
        </span>
      </template>
    </el-dialog>

    <el-container>
      <el-header style="text-align: center">

        <h1>仓库查询</h1>

      </el-header>
      <el-main style="min-height: 400px">
        <div>
          <el-row>
            <el-col :span="20">
              <el-input v-model="searchKW" class="w-50 m-2" size="large" placeholder="Please Input"
                prefix-icon="Search" />
            </el-col>
            <el-col :span="4" style="padding: 2.5px;">
              <el-button type="primary" @click="queryBalance()" :disabled="disable_btn">查询
              </el-button>
            </el-col>
          </el-row>

        </div>
        <el-divider />
        <div style="padding: 14px;">
          <span>持有 {{ balance }} 个 NFT</span>
          <el-progress :percentage="loadingRate">
            <el-button text>{{ loadingNum }} / {{ balance }}</el-button>
            <vue3-json-excel v-if="loadingNum == balance && balance != 0" class="export-excel-wrapper"
              :json-data="tokenList" :fields="json_fields" name="poistion.xls">
              <el-button @click="sendFlag"> 导出excel </el-button>
            </vue3-json-excel>
          </el-progress>
        </div>
        <div>
          <el-row :gutter="10">
            <el-col v-for="(item) in tokenList" :key="item.albums_id" :xs="10" :sm="10" :md="5" :offset="1"
              style="margin-bottom: 20px;">
              <el-card :body-style="{ padding: '0px' }">
                <img :src="item.image_url" class="image" />
                <div style="padding: 14px; text-align: center;">
                  <span>{{ item.albums_name }}</span>
                </div>
                <div style="text-align: center;">
                  <span>持有数量: {{ item.num }}</span>
                </div>
                <div style="padding: 14px; text-align: center;">
                  <span><a :href="item.albums_url" target="_blank">官网跳转</a></span>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>

      </el-main>
      <el-divider />
      <el-footer style="text-align: center">

        <h4>本网页全部数据基于区块链及平台公开数据整理展示，方便个人参考及学习，本网页不会将数据保留，请个人查阅后及24小时内删除，如个人用于非法途径，不当得利损害平台，后果需使用者个人承当全部责任，望周知。</h4>

      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { getWalletBalance, getOwnerToken, getcAttrs } from "@/services/contarct_services"
import { apiGetAlbumInfo, apiGetWallet, apiSetFlag } from "@/apis/queryToken"  //

import { ElLoading, ElMain, ElDialog, ElContainer, ElHeader, ElFooter, ElInput, ElDivider, ElProgress, ElButton, ElRow, ElCol, ElCard, ElMessage } from 'element-plus'
// import 'element-plus/theme-chalk/index.css'

export default {
  name: 'LoginPage',
  components: {
    ElDialog, ElContainer, ElHeader, ElInput, ElMain, ElFooter,
    ElDivider, ElProgress, ElButton, ElRow, ElCol, ElCard
  },
  props: {
    msg: String
  },
  data() {
    return {
      searchKW: "",
      queryKey: "null",
      balance: 0,
      loadingNum: 0,
      loadingRate: 0,
      dialogVisible: true,
      tokenList: [],
      disable_btn: false,
      json_fields: {
        "商品名称": "albums_name",
        "持仓数量": "num",
        "官网链接": "albums_url"
      }
    }
  },
  methods: {
    setQueryKey: function () {
      this.dialogVisible = false
      // if (this.queryKey) {
      //   this.dialogVisible = false
      // } else {
      //   ElMessage({
      //     message: '查询码不能为空',
      //     type: 'warning',
      //   })
      // }
    },
    queryBalance: async function () {
      if (!this.searchKW) {
        ElMessage({
          message: '地址不能为空',
          type: 'warning',
        })
        return
      }
      if (!this.queryKey) {
        this.dialogVisible = true
        return
      }

      this.tokenList = []
      this.tokenMap = {}
      this.loadingNum = 0;
      this.loadingRate = 0;
      const loading_handle = ElLoading.service({ fullscreen: true })
      this.disable_btn = true;
      try {
        let resp = await apiGetWallet(this.queryKey, this.searchKW)
        if (resp["status"] != 0) {
          ElMessage({
            message: resp["msg"],
            type: 'error',
          })
          loading_handle.close()
          this.disable_btn = false
          return
        }
        let num = await getWalletBalance(this.searchKW)
        this.balance = num;
        loading_handle.close()
        await this.queryOwnerToken()
        this.disable_btn = false
      } catch (error) {
        ElMessage({
          message: "后台请求异常,请刷新页面重新查询",
          type: 'error',
        })
        loading_handle.close()
        this.disable_btn = false
      }

    },
    queryOwnerToken: async function () {
      let currLen = this.tokenList.length;
      var that = this;
      for (let index = currLen; index < this.balance; index++) {
        let tokenId = await getOwnerToken(this.searchKW, index)
        let cAttrs = await getcAttrs(tokenId)
        if (this.tokenMap[cAttrs[2]] !== undefined) {
          let idx = this.tokenMap[cAttrs[2]]
          this.tokenList[idx]["num"] += 1
          this.loadingNum += 1;
          this.loadingRate = Math.ceil(that.loadingNum / that.balance * 100)
        } else {
          try {
            let resp = await apiGetAlbumInfo(cAttrs[2])
            resp["albums_url"] = "https://www.ibox.art/zh-cn/item/?id=" + cAttrs[2]
            that.tokenMap[cAttrs[2]] = that.tokenList.length
            resp["num"] = 1
            that.tokenList.push(resp)
            that.loadingNum += 1;
            that.loadingRate = Math.ceil(that.loadingNum / that.balance * 100)
          } catch (error) {
            console.log(error);
            that.tokenMap[cAttrs[2]] = that.tokenList.length
            var data = {
              "albums_url": "https://www.ibox.art/zh-cn/item/?id=" + cAttrs[2],
              "albums_name": "后台查询失败",
              "num": 1,
              "albums_id": cAttrs[2]
            }
            that.tokenList.push(data)
            that.loadingNum += 1;
            that.loadingRate = Math.ceil(that.loadingNum / that.balance * 100)
          }
        }

      }
    },
    sendFlag: async function () {
      await apiSetFlag(this.queryKey)
    }
  }

}
</script>

